body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  background-image: url(/bloodpact/static/media/background.f5162c94.png);
  background-repeat: repeat-x;
}
.links {
  color: black;
}

.nav-links {
  color: lightgray;
}

.links:hover, .nav-links:hover {
  color: red;
}

.links, .links:hover, .nav-links, .nav-links:hover {
  text-decoration: none;
}
#jumbo {
	text-align: center;
}

.jumbo {
	background: url(/bloodpact/static/media/landingPageImg.30f10112.png) no-repeat fixed
		center;
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 820px;
	text-align: center;
}

p.lead {
	font-size: 1.875em;
}

h1.display-4 {
	position: reltaive;
	font-size: 4.5em;
	margin-top: 3em;
	color: white;
}

.scrollDown {
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 5%;
	position: relative;
	top: -50px;
	color: white;
}

.nav-links {
	color: lightgray;
}

#donor-button {
	color: white;
	text-decoration: none;
}

.featurette {
	margin-top: 150px;
}

.featurette-heading-right,
h3.featurette-heading {
	color: red;
}

.lead,
h3.featurette-heading-title,
h1.featurette-heading-title {
	color: white;
}

.lastPerson {
	margin-bottom: 50px;
}

h3 {
	display: inline;
}

.personDescription {
  color: white;
}

.container {
  margin-top: 50px;
  margin-bottom: 50px;
}

.img {
  border-radius: 50%;
  margin-right: 1em;
}
.lastPerson {
	margin-bottom: 50px;
}

.body {
	background-image: url(/bloodpact/static/media/veins-3.24715d9c.png) !important;
	background-size: 250% !important;
	background: black;
}


#sign-up-container {
  margin-top: 3%;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 2%;
  padding-bottom: 3%;
  color: #7F7F7F;
  font-size: 110%;
  width: 45%;
  background-color: white;
  font-family: Aleo-Regular;
  border-radius: 2px;
}

#sign-up-title {
  color:#F9423A;
  font-size: 150%;
  padding-bottom: 5%;
}

#sign-up-button {
  background-color: #F9423A;
  width: 50%;
  height: 200%;
  border-radius: 25px;
}

#button-row {
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
}



#sign-in-container {
  margin-top: 3%;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 2%;
  padding-bottom: 3%;
  color: #7F7F7F;
  font-size: 110%;
  width: 45%;
  background-color: white;
  margin-bottom: 10%;
  font-family: Aleo-Regular;
  border-radius: 2px;
}

#sign-in-title {
  color:#F9423A;
  font-size: 150%;
  padding-bottom: 5%;
}

#sign-in-button {
  background-color: #F9423A;
  width: 50%;
  border-radius: 25px;
}

#button-row {
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
}
.d-block.mb-3.text-muted {
  text-align: center;
}
.dashboard {
  padding: 1%;
}

.dash-left-pane {
  background: #f9423a;
  text-align: center;
  border-radius: 5px;
}
.dash-left-pane-list {
  margin-top: 3%;
}

.isActive {
  background: white;
  color: #f9423a !important;
}

.dash-nav-item {
  color: white !important;
}

.dash-nav-item:hover {
  color: #f9423a !important;
  background: white;
  cursor: pointer;
}

.sub-text {
  color: #f9423a;
}
